<template>
  <div class="bindSale">
    <div class="content">
      <el-form v-if="!saleData.saleName" ref="userData" :model="saleData" label-width="80px">
        <el-form-item label="销售员手机号：" label-width="130px">
          <el-input v-model="willBindSale"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="bindHandler">绑定</el-button>
        </el-form-item>
      </el-form>
      <div v-else class="binded">
        <p>
          <span class="left">销售员手机号：</span>
          <span class="right">{{saleData.salePhone}}</span>
        </p>
        <p>
          <span class="left">真实姓名：</span>
          <span class="right">{{saleData.saleName}}</span>
        </p>
        <p class="colorRed">
          <span class="left">温馨提示：</span>
          <span class="right ">若想重新绑定销售员，可联系客服021-66065505。</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
  import global from '../../../global/global'
  export default {
    name: 'bindSale',
    data () {
      return {
        willBindSale:null,
        saleData:{
          salePhone:'',
          saleName:''
        }
      }
    },
    created(){
      this.queryHandler()
    },
    methods:{
      queryHandler(){
        let params = {
          token: global.getToken()
        }
        global.axiosGetReq('/userPersonalInfo/queryBind',params).then((res) => {
          if(res.data.callStatus === 'SUCCEED'){
            this.saleData = res.data.data
          }
        })
      },
      bindHandler(){
        let params = {
          salePhone: this.willBindSale,
          userPhone: global.getUser().phone,
          token: global.getToken()
        }
        global.axiosPostReq('/userManageList/bind',params).then((res) => {
          if(res.data.callStatus === 'SUCCEED'){
            this.$message({
              message: '绑定成功',
              type: 'success'
            });
            this.queryHandler()
          } else {
            that.$message.error('网络出错，请稍后再试！');
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bindSale{
  height: 550px;
}
.content{
  margin-left: 42px;
  width: 970px;
  color: #5db7e8;
}
.showSearchInfoClass{
  color: #5db7e8;
  line-height: 50px;
}
.showSearchInfoClass span:nth-child(2){
  margin-left: 50px;
}
.binded p .left{
  display: inline-block;
  margin-top: 30px;
}
.binded p:last-child{
  margin-bottom : 170px;
}
.colorRed{
  color:#D81E06;
}
</style>
<style>
.bindSale .el-button--primary{
  display: block;
  margin: auto;
  background-color: #5db7e8;
  border-color: #5db7e8;
  width: 160px;
  line-height: 42px;
  margin-top: 20px;
  padding: 0;
}
.bindSale .el-form-item{
  width: 985px;
}
.bindSale form{
  margin-top: 62px;
}
</style>



